.sign-in__block {
  width: 250px;
  margin: 0 auto;
}

.sign-in__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sign-in__input-block,
.sign-in__submit {
  width: 100%;
}

.sign-in__input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;

  border: 1px solid #e6e6e6;
  border-radius: 5px;
  box-sizing: border-box;
}

.sign-in__btn {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #4481c3;
  color: #fff;
  border: none;
  border-radius: 5px;

  cursor: pointer;
}
