.contacts__search-user {
  margin-bottom: 20px;
}

.contacts__search-input {
  padding: 5px 10px;
  border-radius: 5px;
  border: 2px solid #e6e6e6;
}

.contacts__table {
  border-collapse: collapse;
  width: 100%;
}

.contacts__table-row--titles {
  border-bottom: 1px solid #e6e6e6;
}

.contacts__table-title,
.contacts__table-definition {
  padding: 10px 30px 10px 10px;

  text-align: left;
}

.contacts__table-definition--phone {
  opacity: 0.5;
}

.contacts__table-btn {
  font-weight: 700;

  background-color: transparent;
  border: none;
  cursor: pointer;

  &--edit {
    color: #ffd383;
  }

  &--delete {
    color: red;
  }

  &--save {
    color: green;
  }
}

.contacts__table-input {
  padding: 5px;

  border-radius: 5px;
  border: 2px solid #e6e6e6;
}

.contacts__table-input--name {
  width: 60px;
}

.contacts__table-input--phone {
  width: 90px;
}

.contacts__form {
  max-width: 366px;
}

.contacts__input-text {
  padding: 5px;
  width: 167px;
  margin-bottom: 10px;

  border-radius: 5px;
  border: 2px solid #e6e6e6;
}

.contacts__add-btn {
  display: block;
  width: 100%;
  padding: 10px;

  color: #fff;

  background-color: #4481c3;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
}

@media (min-width: $tablet-width) {
  .contacts {
    width: 768px;
    margin: auto;
  }
}
