.page-header {
  display: flex;
  justify-content: space-between;
  padding: 20px;
}

.page-header__logo {
  font-size: 24px;
  line-height: 20px;
  color: $text-color;

  &::first-letter {
    color: #4481c3;
  }
}

@media (min-width: $tablet-width) {
  .page-header {
    width: 768px;
    margin: auto;
  }
}
