body {
  margin: 0;
  padding: 0;
  font-family: "Rubik", arial, sans-serif;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #48494d;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@font-face {
  font-weight: 400;
  font-family: "Rubik";
  font-style: normal;
  font-display: swap;
  src: url(../fonts/rubik-regular.woff2) format("woff2"), url(../fonts/rubik-regular.woff) format("woff"), url(../fonts/rubik-regular.ttf) format("truetype"); }

@font-face {
  font-weight: 500;
  font-family: "Rubik";
  font-style: normal;
  font-display: swap;
  src: url(../fonts/rubik-medium.woff2) format("woff2"), url(../fonts/rubik-medium.woff) format("woff"), url(../fonts/rubik-medium.ttf) format("truetype"); }

@font-face {
  font-weight: 700;
  font-family: "Rubik";
  font-style: normal;
  font-display: swap;
  src: url(../fonts/rubik-bold.woff2) format("woff2"), url(../fonts/rubik-bold.woff) format("woff"), url(../fonts/rubik-bold.ttf) format("truetype"); }

.page-header {
  display: flex;
  justify-content: space-between;
  padding: 20px; }

.page-header__logo {
  font-size: 24px;
  line-height: 20px;
  color: #48494d; }
  .page-header__logo::first-letter {
    color: #4481c3; }

@media (min-width: 768px) {
  .page-header {
    width: 768px;
    margin: auto; } }

.page-main__title {
  text-align: center; }

.page-main__sign-in {
  padding: 20px; }

.page-main__contacts {
  padding: 20px; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__item {
  display: flex; }

.main-nav__img {
  margin-right: 10px; }

.sign-in__block {
  width: 250px;
  margin: 0 auto; }

.sign-in__form {
  display: flex;
  flex-direction: column;
  align-items: center; }

.sign-in__input-block,
.sign-in__submit {
  width: 100%; }

.sign-in__input {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  box-sizing: border-box; }

.sign-in__btn {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #4481c3;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer; }

.contacts__search-user {
  margin-bottom: 20px; }

.contacts__search-input {
  padding: 5px 10px;
  border-radius: 5px;
  border: 2px solid #e6e6e6; }

.contacts__table {
  border-collapse: collapse;
  width: 100%; }

.contacts__table-row--titles {
  border-bottom: 1px solid #e6e6e6; }

.contacts__table-title,
.contacts__table-definition {
  padding: 10px 30px 10px 10px;
  text-align: left; }

.contacts__table-definition--phone {
  opacity: 0.5; }

.contacts__table-btn {
  font-weight: 700;
  background-color: transparent;
  border: none;
  cursor: pointer; }
  .contacts__table-btn--edit {
    color: #ffd383; }
  .contacts__table-btn--delete {
    color: red; }
  .contacts__table-btn--save {
    color: green; }

.contacts__table-input {
  padding: 5px;
  border-radius: 5px;
  border: 2px solid #e6e6e6; }

.contacts__table-input--name {
  width: 60px; }

.contacts__table-input--phone {
  width: 90px; }

.contacts__form {
  max-width: 366px; }

.contacts__input-text {
  padding: 5px;
  width: 167px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 2px solid #e6e6e6; }

.contacts__add-btn {
  display: block;
  width: 100%;
  padding: 10px;
  color: #fff;
  background-color: #4481c3;
  border: 0;
  border-radius: 5px;
  cursor: pointer; }

@media (min-width: 768px) {
  .contacts {
    width: 768px;
    margin: auto; } }
